import { reduce, reject, includes } from 'ramda';
import './ToggleButton.css';

/**
 * ToggleButton    .
 */
export const TOGGLE_SIZE = {
  COMMON: 'common',
  SMALL: 'small',
};

/**
 *  active  toggle button  .
 * size  small    . default  common .
 * @param { id, label, value, onClick, isChecked, size: TOGGLE_SIZE } param
 * @returns JSX.Element
 */
export const ToggleButton = ({
  id,
  label,
  value,
  onClick,
  isChecked,
  size,
}) => {
  function handleClick() {
    const newIsChecked = !isChecked;
    onClick(newIsChecked, id, value);
  }

  return (
    <button
      className={`btn-toggle ${isChecked ? 'active' : ''} ${size === TOGGLE_SIZE.SMALL ? size : ''}`}
      onClick={handleClick}
    >
      {label}
    </button>
  );
};
export default ToggleButton;

/**
 * item: { id, label, value };
 * items    itemsChecked   ToggleButton  .
 * rowCount   toggleButton  row     .
 * onClick  toggleButton     .   items  parameter   .
 * @param { items: <item>[], rowCount, onClick:(newItemsChecked) => {}, itemsChecked: <item>[] } param0
 * @returns JSX.Element
 */
export const ToggleButtonGroup = ({
  items,
  rowCount,
  onClick,
  itemsChecked,
}) => {
  // to2DArray 

  /**
   * items  2  .
   * toggle button  row     .
   *  rowCount  .
   *
   *   [{ id: 1 }, { id: 2 }, { id: 3 }, { id: 4 }, { id: 5 }, { id:6 }]  6 item   .
   * rowCount  2  2  
   *  => [ [{ id: 1 }, { id: 2 }], [{ id: 3 }, { id: 4 }], [{ id: 5 }, { id: 6 }] ]
   * rowCount  3  2 
   * => [ [{ id: 1 }, { id: 2 }, { id: 3 }], [{ id: 4 }, { id: 5 }, { id: 6 }] ]
   *
   * reduce  ramda       .
   * https://ramdajs.com/docs/#reduce
   */
  const items2DArray = reduce(
    (acc, row) => {
      if (acc.length === 0 || acc[acc.length - 1].length === rowCount) {
        acc.push([]);
      }
      acc[acc.length - 1] = [...acc[acc.length - 1], row];
      return acc;
    },
    [],
    items,
  );

  const handleOnToggleClick = (isChecked, itemChecked) => {
    // reject  ramda        .
    // https://ramdajs.com/docs/#reject
    // isChecked  true  itemsChecked  itemChecked  
    // false  itemsChecked  itemChecked    itemsChecked   onClick   .
    const newItemsChecked = isChecked
      ? [...itemsChecked, itemChecked]
      : reject(el => el.id === itemChecked.id, itemsChecked);

    onClick(newItemsChecked);
  };

  /**
   * includes  ramda        .
   * https://ramdajs.com/docs/#includes
   * itemsChecked  id    item    ToggleButton  isChecked  .
   * @param {*} id
   * @param {*} itemsChecked
   * @returns boolean
   */
  const checkIsChecked = (id, itemsChecked) => {
    const idsChecked = itemsChecked.map(item => item.id);
    return includes(id, idsChecked);
  };

  return (
    <>
      {items2DArray.map((row, idx) => (
        <div key={idx} data-testid={`row${idx}`} className="btn-bunch-row">
          {row.map(item => (
            <ToggleButton
              key={item.id}
              id={item.id}
              label={item.label}
              value={item.value}
              isChecked={checkIsChecked(item.id, itemsChecked)}
              onClick={isChecked => handleOnToggleClick(isChecked, item)}
            />
          ))}
        </div>
      ))}
    </>
  );
};
