import React, { useState } from 'react';

function EditableLabel({ inputWidth = 'auto', buttonWidth = '70px' }) {
  //      state 
  const [inputValue, setInputValue] = useState('');
  const [isEditing, setIsEditing] = useState(false);

  //     
  const handleInputChange = event => {
    setInputValue(event.target.value);
  };

  //   (/)
  const toggleEdit = () => {
    setIsEditing(!isEditing);
  };

  //    
  const handleDelete = () => {
    setInputValue('');
  };

  //     
  const handleSave = () => {
    if (inputValue.trim()) {
      toggleEdit();
    } else {
      alert('Input cannot be empty.');
    }
  };

  //    
  const handleCancel = () => {
    toggleEdit();
  };

  return (
    <div
      style={{
        margin: '20px',
        display: 'flex',
        alignItems: 'center',
        gap: '8px',
      }}
    >
      {isEditing ? (
        <>
          {/*     '', ''   */}
          <input
            type="text"
            value={inputValue}
            onChange={handleInputChange}
            style={{
              flex: `1 0 ${inputWidth}`,
              padding: '8px',
              borderRadius: '4px',
              border: '1px solid #ccc',
            }}
          />
          <button
            onClick={handleCancel}
            style={{
              width: buttonWidth,
              padding: '8px',
              borderRadius: '4px',
              border: '1px solid #ccc',
              backgroundColor: '#ff4500',
              color: 'white',
              cursor: 'pointer',
            }}
          >
            Cancel
          </button>
          <button
            onClick={handleSave}
            style={{
              width: buttonWidth,
              padding: '8px',
              borderRadius: '4px',
              border: '1px solid #ccc',
              backgroundColor: '#007BFF',
              color: 'white',
              cursor: 'pointer',
            }}
          >
            Save
          </button>
        </>
      ) : (
        <>
          {/*     '' ''   */}
          <label
            style={{
              flex: `1 0 ${inputWidth}`,
              padding: '8px',
              border: '1px solid #ccc',
              borderRadius: '4px',
              overflow: 'hidden',
              textOverflow: 'ellipsis',
            }}
          >
            {inputValue || 'No data entered'}
          </label>
          <button
            onClick={toggleEdit}
            style={{
              width: buttonWidth,
              padding: '8px',
              borderRadius: '4px',
              border: '1px solid #ccc',
              backgroundColor: '#28a745',
              color: 'white',
              cursor: 'pointer',
            }}
          >
            Edit
          </button>
          <button
            onClick={handleDelete}
            style={{
              width: buttonWidth,
              padding: '8px',
              borderRadius: '4px',
              border: '1px solid #ccc',
              backgroundColor: '#dc3545',
              color: 'white',
              cursor: 'pointer',
            }}
          >
            Delete
          </button>
        </>
      )}
    </div>
  );
}

export default EditableLabel;
